.App {
	width: 400px;
	margin: 20px 0;
	padding-bottom: 20px;
}

h1.header {
	margin-top: 10px;
	padding: 5px 10px;
	text-align: center;
	font-size: 2rem;
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
	background-color: #fff;
	color: #383cc1;
	-moz-user-select: none;
	-webkit-user-select: none;
	user-select: none;
	box-shadow: 5px 5px 10px #ccc;
}

.container {
	width: 100%;
	min-height: 350px;
	padding: 20px;
	margin-top: 10px;
	box-sizing: border-box;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
		Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	color: #0d0d0d;
	background-color: #fff;
	box-shadow: 5px 5px 10px #ccc;
}

form.getArea {
	margin-top: 10px;
	display: flex;
	flex-direction: column;
}

.inputBox {
	border-bottom: 2px solid #383cc1;
	margin-bottom: 10px;
}

.inputBox input {
	width: 100%;
	padding: 6px 12px;
	outline: none;
	border: none;
	font-size: 0.9rem;
}

.actionArea {
	display: flex;
	justify-content: space-between;
}

.actionArea p {
	align-self: center;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.myButton {
	font-size: 1.2rem;
	padding: 4px 9px;
	color: #fff;
	background-color: #383cc1;
	border: 2px solid #383cc1;
	outline: none;
	border-radius: 2px;
	cursor: pointer;
}

.myButton:hover {
	color: #383cc1;
	background-color: #fff;
}

.todoList {
	padding: 5px 0;
}

@media screen and (max-width: 425px) {
	.App {
		width: 300px;
	}
}
